@use '../../styles/mixin';
@use '../../styles/var' as color;

.tab__container {
  margin-top: 16px;
  width: 100%;
  display: flex;
  gap: 16px;
  border-bottom: 1px solid color.$assistant-gray-color;

  .follow {
    position: relative;
    width: 90px;
    height: 52px;
    padding: 0 1px 13px 1px;

    &:hover {
      border-bottom: 2px solid color.$brand-color;
    }

    .follower,
    .following {
      position: absolute;

      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      @include mixin.font-setting(0.9375rem, 700, #657786);
      line-height: 1.375rem;
    }

    .follower:hover,
    .following:hover {
      color: color.$brand-color;
    }
  }
}

@media screen and (min-width: 800px) {
  .tab__container {
    margin-top: 0;
  }
}
